$(document).ready(() => {
  console.log('Hello Bootstrap4');
// 輪播
  var swiper = new Swiper('.swiper-container', {
    // 更改過
    slidesPerView: 'auto',
    centeredSlides: false,
    //原生
    spaceBetween: 30,
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
    },
  });

  $('#addToCart').click(function(){
    $('#cartList').addClass('active');
  });
});